.main
  display: flex
  flex-direction: column
  gap: 20px

  &_content-wrapper
    display: grid
    // padding: 20px
    // @media (min-width: 768px)
    //   grid-template-columns: 1fr 2fr

  &_content
    @include section-layout

  // &_image-wrapper
  //   max-width: 320px
  //   border-radius: 16px
  //   overflow: hidden
  //   box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25)

  &_image-wrapper
    max-width: 320px
    border-radius: 16px
    perspective: 900px
    transform-style: preserve-3d

  &_image-inner
    position: relative
    border-radius: 16px
    overflow: hidden
    transform-style: preserve-3d
    box-shadow: 0 30px 60px rgba(0, 0, 0, 0.35)
    transition: transform 0.15s ease-out

  &_image
    display: block
    width: 100%
    height: 100%
    object-fit: cover
    filter: grayscale(66%)
    transform: translateZ(40px)
    transition: transform 0.3s ease, filter 0.3s ease

  &_cards-wrapper
    display: flex
    gap: 40px
    flex-wrap: wrap
    justify-content: center
    align-items: center

  &_card
    @include flex-column-center
    box-shadow: $card-shadow
    border-radius: 16px
    padding: 20px
    width: clamp(240px, 40vw, 360px)
    @media (max-width: $mobile-width)
      width: 100%

  &_card-title
    margin: 12px 0
    font-size: clamp(16px, 3vw, 18px)
    font-weight: 600
    color: $text

  &_card-text
    @include subscript-style

  &_about-text
    font-size: 16px
    line-height: 1.6
    color: $text
    margin-bottom: 16px

    &:last-child
      margin-bottom: 0
