.project-card_frame
  position: relative
  width: 100%
  max-width: 280px
  aspect-ratio: 1 / 0.85

  border-radius: clamp(12px, 3vw, 20px)
  background-color: var(--theme-color, transparent)
  box-shadow: 0 0 16px 1px $dark-background inset

  overflow: hidden
  clip-path: inset(-200px -100px 0)

  min-height: 0

.project-card_mask
  position: absolute
  inset: 0
  overflow: visible

.project-card_children
  width: 100%
  height: 100%

  padding: 20px
  border-radius: inherit
  scale: 0.65
  display: flex
  justify-content: center
  align-items: flex-start

  cursor: pointer

  transition: transform 0.45s cubic-bezier(0.22, 1, 0.36, 1)
  will-change: transform

.project-card_frame:hover
  overflow: visible
  & .project-card_children
    transform: translateY(-30%) perspective(900px) rotateX(-12deg) scale(1.25)
    &:has(.video_frame)
      transform: translateY(-100%) perspective(900px) rotateX(10deg)

.project-card_title
  font-size: clamp(18px, 4vw, 28px)
  font-weight: 600
  text-align: center

@media (max-width: 768px)
  .project-card_children
    overflow: visible
    &:not(:has(.video_frame))
      scale: 0.9

  .project-card_frame
    overflow: visible
    &:not(:has(.video_frame))
      aspect-ratio: 1 / 0.5


